import { DECREMENT, DECREMENT_TYPE, INCREMENT, INCREMENT_TYPE, LOGIN_TYPE, LOGOUT, LOGOUT_TYPE, LOGIN } from '../actionTypes';
import { ILoginInfo } from '../../pages/blog/types';

export interface IINCREMENTAction {
  type: INCREMENT_TYPE;
}

export interface IDECREMENTAction {
  type: DECREMENT_TYPE;
}

export interface ILOGINAction{
  userInfo: ILoginInfo;
  type: LOGIN_TYPE;
}

export interface ILOGOUTAction{
  type: LOGOUT_TYPE
}

//  modifyAction  IINCREMENTAction  IDECREMENTAction 
export type ModifyAction = IINCREMENTAction | IDECREMENTAction | ILOGINAction | ILOGOUTAction;


//  state 
export const increment = (): IINCREMENTAction => ({
  type: INCREMENT,
})

//  state 
export const decrement = (): IDECREMENTAction => ({
  type: DECREMENT
})

//
export const login = (userInfo: ILoginInfo): ILOGINAction => ({
  userInfo,
  type: LOGIN
})

//
export const logout = (): ILOGOUTAction => ({
  type: LOGOUT
})

