import axios from 'axios'
import { message, Modal } from 'antd';
const ConfigBaseURL = 'http://localhost:7000/' //env
let loadingInstance = null //loading
//createaxios
export const Service = axios.create({
  timeout: 7000, // 
  baseURL: ConfigBaseURL,
//   method: 'post',
//   headers: {
//     'Content-Type': 'application/json;charset=UTF-8'
//   }
})
// const address = '127.0.0.1/7001'
Service.interceptors.request.use(function (config) {
    // Do something before request is sent
    // console.log(config);

  config.url?.indexOf('/admin/') ?
    config.headers.token = localStorage.getItem('token') :
    config.headers.token = localStorage.getItem('adminToken')
    return config;
  }, function (error) {
    // Do something with request error
    return Promise.reject(error);
  });

// Add a response interceptor
Service.interceptors.response.use(
  res => {
    // res.status === 200 ?
    console.log(res);
    
    if (res.status === 200) {
        if (res.data.status && res.data.status === 401) {
          console.log(res.data.status);
          
         Modal.confirm({
           title: '',
           content: ``,
           onOk: () => {
             localStorage.removeItem('userInfo')
             localStorage.removeItem('token')

            //  window.h
             window.location.reload()
                }
              })
        }
        return Promise.resolve(res.data)
      } else {
        return Promise.reject(res)
      }
      
     } 
    ,function (error) {
    // Do something with response error
    return Promise.reject(error);
  });
class Request {
     post = async (url:string, data?:object,) =>{
        try {
            const result = await Service.post(url, data)
            return result

        } catch (error) {
            
        }

  }
  
  get = async (url: string) => {
    try {
      const result = await Service.get(url)
      return result
    } catch (error) {
      console.log(error);
      
    }
  }
}

export default Request